






import { Component, Prop, Vue, Emit } from 'vue-property-decorator';

@Component
export default class App extends Vue {
    @Emit()
    private Ban() {
        document.body.addEventListener(
            'touchmove',
            (e: any) => {
                e.preventDefault(); // 阻止默认的处理方式(阻止下拉滑动的效果)
            },
            { passive: false },
        );
    }
    private created() {
        // this.Ban();
    }
}
